<template>
  <div class="gauge-chart"
       ref="chart">
    <div ref="a12"></div>
  </div>
</template>
<script>
const echarts = require('echarts/lib/echarts')
require('echarts/lib/chart/gauge/GaugeSeries')
require('echarts/lib/chart/gauge/GaugeView')
require('echarts/lib/chart/gauge/PointerPath')
// import * as echarts from 'echarts/core'
// import {
//     GaugeChart
// } from 'echarts'
// import {
//     CanvasRenderer
// } from 'echarts/renderers'

// echarts.use(
//     [GaugeChart, CanvasRenderer]
// )
export default {
  name: 'GaugeChart',
  props: {
    value: {
      default: 70
    }
  },
  data() {
    return {
      options: {
        series: [{
          type: 'gauge',
          startAngle: 210,
          endAngle: -30,
          progress: {
            show: true,
            width: 18,
            roundCap: true,
            itemStyle: {
              color: {
                type: 'linear',
                x: 0,
                y: 1,
                x2: 1,
                y2: 1,
                colorStops: [
                  {
                    offset: 0, color: '#EA5E37' // 0% 处的颜色
                  },
                  {
                    offset: 0.7, color: '#F0BE38' // 0% 处的颜色
                  },
                  {
                    offset: 1, color: '#65D22D' // 100% 处的颜色
                  }
                ],
                globalCoord: false // 缺省为 false
              }
            }
          },
          axisLine: {
            roundCap: true,
            lineStyle: {
              width: 18,
              color: [[1, '#f1f1f1']]
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: false
          },
          axisLabel: {
            show: false
          },
          pointer: {
            show: false
          },
          detail: {
            valueAnimation: true,
            fontSize: 14,
            offsetCenter: [0, 0]
          },
          data: [{
            value: 70
          }]
        }]
      }
    }
  },
  mounted() {
    this.options.series[0].data[0].value = this.value
    // let colorStops = this.options.series[0].progress.itemStyle.color.colorStops
    // if(this.value <= 50) {
    //   colorStops = colorStops.slice(0, 1)
    // }else if(this.value < 80) {
    //   colorStops = colorStops.slice(0, 2)
    // }
    let option = {
      series: [{
        type: 'gauge',
        startAngle: 210,
        endAngle: -30,
        progress: {
          show: true,
          width: 12,
          roundCap: true,
          itemStyle: {
            color: {
              type: 'linear',
              x: 0,
              y: 1,
              x2: 1,
              y2: 1,
              colorStops: [
                {
                  offset: 0, color: '#EA5E37' // 0% 处的颜色
                }
                  // {
                  //   offset: 0, color: '#EA5E37' // 0% 处的颜色
                  // },
                  // {
                  //   offset: 0, color: '#EA5E37' // 0% 处的颜色
                  // },
                  // {
                  //   offset: 0.75, color: '#F0BE38' // 0% 处的颜色
                  // },
                  // {
                  //   offset: 1, color: '#65D22D' // 100% 处的颜色
                  // }
              ],
              globalCoord: false // 缺省为 false
            }
          }
        },
        axisLine: {
          roundCap: true,
          lineStyle: {
            width: 12,
            color: [[1, '#f1f1f1']]
          }
        },
        axisTick: {
          show: false
        },
        splitLine: {
          show: false
        },
        axisLabel: {
          show: false
        },
        pointer: {
          show: false
        },
        detail: {
          valueAnimation: true,
          fontSize: 80,
          offsetCenter: [0, 0]
        },
        data: [{
          value: 40
        }]
      }]
    }

    // this.options.series[0].progress.itemStyle.color.colorStops = colorStops
    let chart = echarts.init(this.$el)
    chart.setOption(option)
  }
}
</script>
<style scoped>
.gauge-chart {
  height: 300px;
}
</style>



